import { useSelector } from "react-redux";
import "./UserDetailsPage.scss";

export const UserDetailsPage = ({ history }) => {
  const loggedUser = useSelector((state) => state.userReducer.user);

  return (
    <div className="user-details">
      <img src={loggedUser.img} alt="" />
      <p>Fullname: {loggedUser.fullname}</p>
      <p>Username: {loggedUser.username}</p>
      <p>Phome: {loggedUser.phone}</p>
      <p>Email: {loggedUser.email}</p>
    </div>
  );
};
